body #main{width:min(1160px, 100% - 4rem);margin-inline:auto;padding:2% 0 2% 0;height:calc(100vh - 10px);display:grid;grid-template-columns:minmax(10px, 1fr) minmax(10px, 3fr);grid-auto-rows:min-content;gap:4em 50px}body #main header{grid-column:2/-1}body #main aside{grid-column:1/2;grid-row:2/3}body #main .content{grid-column:2/2}body #main footer{grid-column:1/-1}@media only screen and (max-width: 800px){body #main{padding:2rem;grid-template-columns:100%;grid-template-rows:auto;gap:0}body #main>*{grid-column:1/-1 !important;grid-row:auto !important}}@media print{body #main{padding:2rem;grid-template-columns:100%;grid-template-rows:auto;gap:0}body #main>*{grid-column:1/-1 !important;grid-row:auto !important}}:root{color-scheme:light dark}body{--text-color:#000;--background-color:#fff;--background-color-bubble:#f5f5f5;--accent-color:initial;--link-color:blue;--link-color-visited:blue;--color-orange:#cd8500;--color-dark-orange:#613f00}body.dark{--text-color:#f5f5f5;--background-color:#282828;--background-color-bubble:#181818;--link-color:lightblue;--link-color-visited:lightblue}:root{--header-font:system-ui, sans-serif;--body-font:Charter, "Bitstream Charter", "Sitka Text", Cambria, serif}::selection{background:var(--color-orange);color:#fff;text-shadow:1px 1px 3px var(--color-dark-orange)}*{text-rendering:optimizeLegibility;font-variant-ligatures:common-ligatures;font-variant-numeric:oldstyle-nums stacked-fractions;text-underline-offset:0.13em;text-decoration-skip-ink:auto}body{font-size:18px;line-height:1.7em;font-family:var(--body-font);color:var(--text-color);background-color:var(--background-color);transition:background-color 0.125s}a{cursor:pointer;color:var(--link-color)}a:hover{text-decoration:none}a:visited{color:var(--link-color-visited)}h1,h2,h3,h4{font-family:var(--header-font);font-variant-ligatures:common-ligatures;font-weight:900}@media only screen and (max-width: 800px){.content{margin:5em 0 0 0}}@media print{.content{margin:5em 0 0 0}}aside ul{list-s